.toparea
  height: calc(100vh + 190px)
  position: relative
  box-sizing: border-box
  padding-bottom: 190px
  .header
    position: absolute
    top: 0
    left: 0
    right: 0

.toparea__inner
  height: 100%
  width: 100%
  min-height: 680px
  display: table
  table-layout: fixed
  position: relative
  z-index: 1

.toparea__center
  position: relative
  z-index: 1
  display: table-cell
  width: 100%
  height: 100%
  padding-top: 50px
  box-sizing: border-box
  vertical-align: middle
  text-align: center

.toparea__video
  position: absolute
  z-index: 0
  top: 0
  left: 0
  width: 100%
  height: 100%
  background: #646566 img-url('toparea-bg.jpg') no-repeat center
  background-size: cover


.intro__top-text
  display: inline-block
  position: relative
  left: 10px
  +f(futura-demi, 24, 1000)
  color: rgba(#FFF, 0.4)
  line-height: 1
  text-transform: uppercase

.intro__large-text
  display: block
  margin: 10px 0
  +f(futura-demi, 150, 100)
  line-height: 1

.intro__bottom-text
  display: block
  +f(agora-regular, 12, 100)
  line-height: 1.83
  text-transform: uppercase


.play-button
  display: block
  width: 50px
  height: 50px
  margin: 40px auto
  background: none
  color: #FFF
  font-size: 15px
  border-radius: 50%
  border: 1px solid rgba(#F7F7F7, 0.3)
  +trans
  transition-property: border-color, color
  &:before
    position: relative
    left: 2px
  &:hover,
  &:focus
    color: $color-green
    border-color: $color-green


.number
  display: inline-block
  vertical-align: middle
  position: relative
  padding-left: 60px
  text-align: left
  line-height: 1

  &.-color-dark
    color: $color-text
    .number__value
      // color: inherit
      // +no-text-gradient
    .number__sub
      color: inherit
    .number__text,
    .number__count,
    .number__sup
      color: inherit

  &.-text-lg
    .number__value
      font-size: 200px

  &.-text-sm
    .number__value
      font-size: 120px

.number__count
  display: inline-block
  position: absolute
  top: 0
  left: 0
  +f(futura-demi, 14, 100)
  line-height: 1
  color: $color-grey-lighter

.number__sup
  position: absolute
  top: -7px
  left: 0
  +f(fd, 30)
  line-height: 1
  color: $color-grey-lighter

.number__value
  display: inline-block
  vertical-align: bottom
  +f(futura-demi, 150, 50)
  line-height: 0.73
  // +text-gradient

.number__sub
  display: inline-block
  vertical-align: bottom
  +f(futura-demi, 24, 100)
  line-height: 1
  text-transform: uppercase
  color: $color-green
  &.-type-currency
    font-size: 30px

.number__text
  display: block
  margin-top: 25px
  +f(agora-regular, 18)
  line-height: 1.22
  color: $color-grey-lighter



.facts
  padding: 30px 0 0
  position: relative

.facts__title
  position: absolute
  left: 50%
  bottom: 100%
  margin-bottom: 70px
  +f(futura-demi, 30, 2000)
  line-height: 1
  color: $color-grey
  text-transform: uppercase


.facts__row
  position: relative
  height: 280px
  margin-bottom: 60px
  +clr


.facts__left,
.facts__right
  position: absolute
  box-sizing: border-box
  top: 0
  height: 100%
  width: 50%
  line-height: 280px

.facts__left
  right: 50%
  text-align: right
  margin-right: 95px
  .number
    width: 395px

.facts__right
  left: 50%
  padding-left: 95px
  text-align: left


.stats
  margin-top: 170px

.stats__title
  margin-bottom: 60px
  +f(futura-demi, 60, 100)
  line-height: 1
  text-transform: uppercase
  color: $color-grey
  > small
    +f(agora-regular, 18)
    display: block
    margin-top: 10px
    text-transform: none
    color: #555
    letter-spacing: 0

.stats__buttons
  margin: 50px 0
  .btn-round
    &:not(:last-child)
      margin-right: 36px

.stats__footer
  margin-top: 20px
  text-align: right
  +f(ar, 14)
  color: $color-grey
  line-height: 1.57
  a
    color: inherit
    text-transform: uppercase
    font-size: 12px
  b
    +f(ab)

.stats__graph
  height: 400px
  background: $color-grey


.salary
  position: relative
  height: 320px
  margin-top: 135px

.salary__title
  +f(fd, 40, 50)
  line-height: 1
  color: $color-grey-lighter
  text-transform: uppercase
  small
    display: block
    margin-top: 10px
    line-height: 1.25
    +f(ar, 24)
    color: $color-grey
    letter-spacing: 0
    text-transform: none

.salary__left
  position: absolute
  top: 0
  left: 20px
  width: 40%
  height: 100%
  box-sizing: border-box

.salary__right
  position: absolute
  top: 0
  left: 41.7%
  right: -1000px
  height: 100%
  box-sizing: border-box

.salary__left-content
  box-sizing: border-box
  padding: 40px 40px 0 0

.salary__right-content
  padding: 65px 0 0 75px
  text-align: left
  max-width: 640px
  box-sizing: border-box


.courses
  margin-top: 150px

.courses__title
  +f(fd, 30, 2000)
  line-height: 1.667
  color: $color-grey
  text-transform: uppercase

.courses__container
  position: relative
  +clr

.courses__left,
.courses__right
  width: percentage(335/1140)
  box-sizing: border-box
  padding: 60px 50px 110px
  opacity: 0.6
  +trans(opacity)

.courses__left
  float: left
  padding-left: 0

.courses__right
  float: right
  padding-right: 0

.courses__footer
  text-align: center
  margin-top: 55px


.courses-promo
  max-width: 500px
  margin: auto
  text-align: left
  +clr

.courses-promo__text
  float: left
  width: percentage(200/500)
  +f(fh, 16, 100)
  line-height: 1.4
  color: #FFF
  text-transform: uppercase
  strong
    display: block
    font-size: 24px
    line-height: 1.1
    font-weight: normal

.courses-promo__value
  float: right
  width: percentage(300/500)
  +f(fh, 90, 100)
  line-height: 0.75
  white-space: nowrap
  sub
    font-size: 18px
    text-transform: uppercase
    margin-left: -20px
    letter-spacing: 0.1em


.courses__center
  position: absolute
  top: 0
  left: percentage(335/1140)
  width: percentage(470/1140)
  box-sizing: border-box
  padding: 25px 50px 80px
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15)



.course
  color: $color-grey
  &.-primary
    .course__title
      color: #FFF
    .course__prise
      font-size: 48px
    .course__footer
      margin-top: 60px

.course__title
  display: block
  +f(fd, 30, 100)
  line-height: 1
  text-transform: uppercase

.course__subtitle
  display: block
  +f(ar, 18)
  line-height: 1.667

.course__prise
  display: block
  margin: 30px 0 40px
  +f(fd, 36, 100)
  line-height: 1
  sub
    font-size: 18px
    text-transform: uppercase

.course__prise-old
  display: block
  +f(fb, 18, 100)
  text-transform: uppercase
  line-height: 1.667

.course__head
  margin-bottom: 68px
  position: relative
  margin: -68px -27px 0
  padding: 68px 27px 20px

.course__footer
  margin-top: 50px


.course-info
  color: $color-grey-lighter
  line-height: 1.667
  margin: 10px 0

.course-info__label
  display: block
  +f(fb, 12, 100)
  text-transform: uppercase
  color: #AAA

.course-info__value
  +f(ar, 18)


.workdays
  +f(ar, 12)
  text-transform: uppercase
  > li
    display: inline-block
    vertical-align: baseline
    color: #AAA
    margin-right: 8px
  em
    +f(fd, 18, 100)
    color: $color-grey-lighter
    font-style: normal

.complexity
  display: inline-block
  position: relative
  width: 100%
  height: 8px
  box-sizing: border-box
  border-radius: 4px
  background: rgba(#AAA, 0.1)
  overflow: hidden
  .complexity__bar,
  &:before
    content: ""
    position: absolute
    top: 0
    left: 0
    width: 70%
    height: 100%
    border-radius: 4px
    background-color: $color-green
    background-image: $gradient-diagonal

  &.-beginner
    .complexity__bar,
    &:before
      width: 45%

  &.-middle
    .complexity__bar,
    &:before
      width: 65%

  &.-pro
    .complexity__bar,
    &:before
      width: 85%
